.spanMenu {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    border: 1px solid;
    border-radius: 7px;
    height: 30px;
}

.spanMenu:hover {
    background-color: #2ab7a9;
}

.buttonFooter {
    display: flex;
    justify-content: space-between;
}

.todoListFooter button {
    border-radius: 7px;
    border: 1px solid black;
    width: 90px;
    height: 30px;
}
